<template>
  <div class="login">
    <img src="../assets/logo.png" alt="" class="logo">
    <div class="login-box">
      <div class="input-suffix">
        <el-input
          placeholder="请输入用户名"
          prefix-icon="el-icon-user"
          type="text"
          v-model="user">
        </el-input>
      </div>
      <div class="input-suffix">
        <el-input
          placeholder="请输入密码"
          prefix-icon="el-icon-lock"
          type="password"
          maxlength="16"
          minlength="6"
          v-model="passwd">
        </el-input>
      </div>
      <el-button type="primary" @click="login">登陆</el-button>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import md5 from 'js-md5'

export default {
  name: 'Login',
  data () {
    return {
      user: '',
      passwd: ''
    }
  },
  created () {
    sessionStorage.removeItem('isLogin')
    this.setUser('')
  },
  methods: {
    ...mapMutations(['setUser']),
    login () {
      const md5Passwd = 'e10adc3949ba59abbe56e057f20f883e'
      if (this.user !== 'test') return this.$message.error('用户名错误')
      if (md5(this.passwd) !== md5Passwd) return this.$message.error('密码错误')
      this.setUser(this.user)
      sessionStorage.setItem('isLogin', true)
      this.$message({
        message: '欢迎您，' + this.user,
        type: 'success',
        duration: 1500,
        onClose: () => {
          this.$router.push('/')
        }
      })
    }
  }
}
</script>

<style scoped>
.logo {
  display: block;
  width: 140px;
  margin: 50% auto;
}

.login-box {
  width: 280px;
  height: 162px;
  background-color: #FFFFFF;
  border-radius: 5px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.input-suffix:nth-child(2) {
  margin-top: 20px;
}

.el-button--primary {
  width: 100%;
  margin-top: 20px;
}
</style>
