import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    title: '首页',
    user: ''
  },
  mutations: {
    goPageTitle (state, val) {
      state.title = val
    },
    setUser (state, val) {
      state.user = val
    }
  },
  actions: {},
  modules: {}
})
